// frontend/pages/register.js
import { useState } from 'react';
import { useRouter } from 'next/router';
import api from '../utils/api';
import Navbar from '../components/Navbar';

export default function Register(){
  const [name,setName]=useState('');
  const [email,setEmail]=useState('');
  const [password,setPassword]=useState('');
  const [role,setRole]=useState('buyer');
  const router = useRouter();

  const submit = async () => {
    try {
      await api.post('/auth/register', { name, email, password, role });
      alert('Registered! Please login.');
      router.push('/login');
    } catch(err) {
      alert(err.response?.data?.message || 'Register failed');
    }
  }

  return (
    <>
      <Navbar />
      <div className="container mx-auto max-w-md mt-12">
        <div className="card">
          <h2 className="text-2xl font-bold mb-4">Create Account</h2>
          <input className="w-full border p-2 rounded mb-2" placeholder="Name" value={name} onChange={e=>setName(e.target.value)} />
          <input className="w-full border p-2 rounded mb-2" placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)} />
          <input className="w-full border p-2 rounded mb-2" placeholder="Password" type="password" value={password} onChange={e=>setPassword(e.target.value)} />
          <select value={role} onChange={e=>setRole(e.target.value)} className="w-full border p-2 rounded mb-3">
            <option value="buyer">Buyer</option>
            <option value="seller">Seller</option>
          </select>
          <button onClick={submit} className="btn btn-primary w-full">Register</button>
        </div>
      </div>
    </>
  );
}

